 #loading-wrapper {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   z-index: 5000;
   background: #fff;
   display: flex;
   align-items: center;
   justify-content: center;
 }
 #loading-wrapper .loader {
   width: fit-content;
   font-size: 2rem;
   font-weight: 600;
   background: radial-gradient(
       circle closest-side,
       #111018 94%,
       rgba(0, 0, 0, 0)
     )
     right/calc(200% - 1em) 100%;
   animation: l24 1s infinite alternate linear;
 }
 #loading-wrapper .loader::before {
   content: "Loading...";
   line-height: 1em;
   color: rgba(0, 0, 0, 0);
   background: inherit;
   background-image: radial-gradient(circle closest-side, #ffffff 94%, #111018);
   -webkit-background-clip: text;
   background-clip: text;
 }
